<template>
  <div id="wzd">
    <div class="clearfix con">
      <div class="fl con_l">
        <input type="text" v-model.lazy="val" placeholder="关键字" />
      </div>
      <div class="fl con_r">
        <div>
          <h4
            style="margin:0 30px; text-align:center; color:#003399;padding:50px 0 10px 0;font-size:22px;border-bottom:1px #ccc solid;"
          >搜索结果页面</h4>
          <p style="text-align:center;padding-top:40px;font-size:18px;">没有符合条件的结果</p>
          <p style="text-align:center;padding-top:40px;font-size:18px;">这里是模拟数据</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "wzd",
  data() {
    return {
      val: ""
    };
  },
  created() {
      this.val=this.$route.params.val
  },
  watch: {
    val(a, b) {
      if (a != b) {

      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
a {
  text-decoration: none;
  color: #000;
}
.clearfix:after,
.clearfix:before {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
.clear {
  zoom: 1;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
#wzd .con {
  min-height: 600px;
}
#wzd .con_l {
  background: #f0f0f0;
  min-height: 600px;
  width: 20%;
  padding: 26px 20px;
  box-sizing: border-box;
}
#wzd .con_l input {
  height: 34px;
  width: 100%;
  font-size: 18px;
  padding-left: 10px;
  box-sizing: border-box;
}
#wzd .con_r {
  background: #fff;
  min-height: 600px;
  width: 80%;
}
</style>



