<template>
  <div id="honor">
    <div style="height:240px;"><img style="height:100%;width:100%;" src="https://ccdn.goodq.top/caches/82d81811e3bb21e2c1f17161bf43e1dd/aHR0cDovLzU2ZGU3ODcwYTliMTcudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxNi8wMy9mYjJkMGFjNjk1YjRiMjU1Y2NlOTJkOTc4ZWI2MzkyMS5qcGc_p_p100_p_3D.jpg" alt=""></div>
    <div class="clearfix con">
      <div class="fl con_l">
          <input type="text" v-model.lazy="val" placeholder="关键字">
          <div>
              <h5>行业荣誉</h5>
              <div class="con_l_item">
                  <p><i class="el-icon-caret-right"></i><router-link to="/honor/honor1/1">行业荣誉</router-link></p>
              </div>
          </div>
      </div>
      <div class="fl con_r">
          <h4>行业荣誉</h4>
          <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'honor',
  data () {
    return {
     val:'',
    }
  },
  watch:{
    val(a,b){
      if(a!=b){
        this.$router.push({name:'wzd',params:{val:this.val}})
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
a {
  text-decoration: none;
  color: #000;
}
.clearfix:after,
.clearfix:before {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
.clear {
  zoom: 1;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
#honor .con{
    height:600px;
}
#honor .con_l{
    background:#f0f0f0;
    height:100%;
    width:20%;
    padding:26px 20px;
    box-sizing: border-box;
}
#honor .con_l input{
    height:34px;
    width:100%;
    font-size:18px;
    padding-left:10px;
    box-sizing: border-box;
}
#honor .con_r{
    background:#fff;
    height:100%;
    width:80%;
}
#honor h5{
    font-size:18px;
    padding: 20px 4px 10px 4px;
    border-bottom:4px #000 solid;
    margin-bottom:6px;
} 
#honor .con_l_item p{
    border-bottom:1px #ccc solid;
    color:#676262;
    font-size:15px;
    padding:8px 10px;
}
#honor .con_l_item p:hover{
    color:#15615a;
}
#honor .con_l_item p i{
    color:#ccc;
}
#honor .con_r{
    padding:30px 20px;
    box-sizing: border-box;
}
#honor .con_r h4{
    font-size:20px;
    color:#15615a;
    padding-bottom:16px;
    margin-bottom:16px;
    border-bottom:1px #ccc solid;
}
</style>



