<template>
  <div id="login">
    <div class="clearfix con">
      <div class="fl con_l">
          <input type="text" placeholder="关键字">
          <div>
              <h5>会员专区</h5>
              <div class="con_l_item">
                  <p><i class="el-icon-caret-right"></i><router-link to="/vip/vip2">申请入会</router-link></p>
                  <p><i class="el-icon-caret-right"></i><router-link to="/vip/vip3">协会杂志</router-link></p>
              </div>
          </div>
      </div>
      <div class="fl con_r">
          <div>
            <h4 style="margin:0 30px; text-align:center; color:#003399;padding:50px 0 10px 0;font-size:22px;border-bottom:1px #ccc solid;">会员登录</h4>
            <div style="width:70%; margin:30px auto 140px auto;border:1px #ccc solid; border-radius:4px;padding:20px; box-sizing:border-box;">
                <div style="margin-bottom:10px;">
                    <label for="a"><span style="width:50px;display:inline-block;">用户名</span></label>
                    <el-input id="a" style="width:70%;margin-left:10%;" v-model="user" placeholder="请输入用户名"></el-input>
                </div>
                <div>
                    <label for="a"><span style="width:50px;display:inline-block;">密码</span></label>
                    <el-input id="a" style="width:70%;margin-left:10%;" v-model="pass" placeholder="请输入密码"></el-input>
                </div>
                <div style="margin-top:20px;padding-top:16px;border-top:1px #ccc solid;">
                    <el-button size="small" style="padding:8px 14px; background:#000; color:#fff;">登录</el-button>
                    <el-button size="small" style="padding:8px 14px;">注册</el-button>
                    <p class="fr" style="font-size:14px;">忘记密码？点这里找回</p>
                </div>
            </div>
            <div style="padding:0 30px;">
                <p>说明:</p>
                <p>1.实际使用之前，需配置用户管理组件中的相关邮件。详情请参考教程：《用户管理中相关邮件的配置》</p>
                <p>2.新用户注册后的审核方式有三种：自动核准、邮件激活、需要管理员批准（即人工审核），详情可参考教程
                    <a href="#" style="color:#52c7c7;">《如何设置用户注册后的审核方式》</a>
                </p>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
     user:'',
        pass:'',
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
a {
  text-decoration: none;
  color: #000;
}
.clearfix:after,
.clearfix:before {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
.clear {
  zoom: 1;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
#login .con{
    min-height:600px;
}
#login .con_l{
    background:#f0f0f0;
    min-height:700px;
    width:20%;
    padding:26px 20px;
    box-sizing: border-box;
}
#login .con_l input{
    height:34px;
    width:100%;
    font-size:18px;
    padding-left:10px;
    box-sizing: border-box;
}
#login .con_r{
    background:#fff;
    min-height:700px;
    width:80%;
}
#login h5{
    font-size:18px;
    padding: 20px 4px 10px 4px;
    border-bottom:4px #000 solid;
    margin-bottom:6px;
} 
#login .con_l_item p{
    border-bottom:1px #ccc solid;
    color:#676262;
    font-size:15px;
    padding:8px 10px;
}
#login .con_l_item p:hover{
    color:#15615a;
}
#login .con_l_item p i{
    color:#ccc;
}
</style>



