        this.$router.push({name:'wzd',params:{val:this.val}})
      }
    }
  },
  mounted() {
    //渲染后加载地图
    var map = new AMap.Map("container", {
      zoom: 11, //级别
      center: [116.397428, 39.90923], //中心点坐标
      viewMode: "3D" //使用3D视图
    });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
a {
  text-decoration: none;
  color: #000;
}
.clearfix:after,
.clearfix:before {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
.clear {
  zoom: 1;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
#links .con {
  height: 650px;
}
#links .con_l {
  background: #f0f0f0;
  height: 100%;
  width: 20%;
  padding: 26px 20px;
  box-sizing: border-box;
}
#links .con_l input {
