<template>
  <div id="login1">
    <div class="clearfix con">
      <div class="fl con_l">
          <input type="text" placeholder="关键字">
          <div>
              <h5>会员专区</h5>
              <div class="con_l_item">
                  <p><i class="el-icon-caret-right"></i><router-link to="/vip/vip2">申请入会</router-link></p>
                  <p><i class="el-icon-caret-right"></i><router-link to="/vip/vip3">协会杂志</router-link></p>
              </div>
          </div>
      </div>
      <div class="fl con_r">
          <div>
            <h4 style="margin:0 30px; text-align:center; color:#003399;padding:50px 0 10px 0;font-size:22px;border-bottom:1px #ccc solid;">注册</h4>
            <div style="width:70%; margin:30px auto 40px auto;border:1px #ccc solid; border-radius:4px;padding:20px; box-sizing:border-box;">
                <div style="margin-bottom:10px;">
                    <label for="a"><span style="font-size:14px;width:110px;display:inline-block;">用户名</span></label>
                    <el-input id="a" style="width:70%;" v-model="user" placeholder="请输入用户名"></el-input>
                </div>
                <div style="margin-bottom:10px;">
                    <label for="b"><span style="font-size:14px;width:110px;display:inline-block;">密码</span></label>
                    <el-input id="b" style="width:70%;" v-model="pass" placeholder="请输入密码"></el-input>
                </div>
                <div style="margin-bottom:10px;">
                    <label for="c"><span style="font-size:14px;width:110px;display:inline-block;">再次输入密码</span></label>
                    <el-input id="c" style="width:70%;" v-model="pass1" placeholder="请再次输入密码"></el-input>
                </div>
                <div style="margin-bottom:10px;">
                    <label for="d"><span style="font-size:14px;width:110px;display:inline-block;"></span></label>
                    <div style="display:inline-block;">
                        <p style="font-size:14px;margin-bottom:10px;">密码强度</p>
                        <div style="height:8px;width:240px;background:#ccc;"></div>
                    </div>
                </div>
                <div style="margin-bottom:10px;">
                    <label for="e"><span style="font-size:14px;width:110px;display:inline-block;">姓名</span></label>
                    <el-input id="e" style="width:70%;" v-model="name" placeholder="请输入姓名"></el-input>
                </div>
                <div style="margin-bottom:10px;">
                    <label for="a"><span style="font-size:14px;width:110px;display:inline-block;">手机</span></label>
                    <el-input id="a" style="width:70%;" v-model="tel" placeholder="请输入手机号"></el-input>
                </div>
                <div style="margin-bottom:10px;">
                    <label for="f"><span style="font-size:14px;width:110px;display:inline-block;">电子邮箱</span></label>
                    <el-input id="f" style="width:70%;" v-model="email" placeholder="请输入邮箱账号"></el-input>
                </div>
                <div style="margin-bottom:10px;">
                    <label for="g"><span style="font-size:14px;width:110px;display:inline-block;">验证问题</span></label>
                    <el-input id="g" style="width:70%;" v-model="num" placeholder="请问3+6等于几？"></el-input>
                </div>
                <div style="margin-top:20px;padding-top:16px;border-top:1px #ccc solid;">
                    <el-button size="small" style="background:#000; color:#fff;padding:8px 14px;">注册</el-button>
                    <p class="fr" style="font-size:14px;border:1px #ccc solid;padding:4px 10px; border-radius:4px;">已有账号了？请登录</p>
                </div>
            </div>
            <div style="padding:0 30px;">
                <p>说明:</p>
                <p>1.实际使用之前，需配置用户管理组件中的相关邮件。详情请参考教程：《用户管理中相关邮件的配置》</p>
                <p>2.新用户注册后的审核方式有三种：自动核准、邮件激活、需要管理员批准（即人工审核），详情可参考教程
                    <a href="#" style="color:#52c7c7;">《如何设置用户注册后的审核方式》</a>
                </p>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'login1',
  data () {
    return {
     user:'',
        pass:'',
        pass1:'',
        name:'',
        tel:'',
        email:'',
        num:'',
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
a {
  text-decoration: none;
  color: #000;
}
.clearfix:after,
.clearfix:before {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
.clear {
  zoom: 1;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
#login1 .con{
    min-height:700px;
}
#login1 .con_l{
    background:#f0f0f0;
    min-height:840px;
    width:20%;
    padding:26px 20px;
    box-sizing: border-box;
}
#login1 .con_l input{
    height:34px;
    width:100%;
    font-size:18px;
    padding-left:10px;
    box-sizing: border-box;
}
#login1 .con_r{
    background:#fff;
    min-height:840px;
    width:80%;
}
#login1 h5{
    font-size:18px;
    padding: 20px 4px 10px 4px;
    border-bottom:4px #000 solid;
    margin-bottom:6px;
} 
#login1 .con_l_item p{
    border-bottom:1px #ccc solid;
    color:#676262;
    font-size:15px;
    padding:8px 10px;
}
#login1 .con_l_item p:hover{
    color:#15615a;
}
#login1 .con_l_item p i{
    color:#ccc;
}
</style>



