import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

import about from '@/components/about'
import about1 from '@/components/about1'
import about2 from '@/components/about2'
import about3 from '@/components/about3'

import vip from '@/components/vip'
import vip1 from '@/components/vip1'
import vip2 from '@/components/vip2'
import vip3 from '@/components/vip3'

import study from '@/components/study'
import study1 from '@/components/study1'

import policy from '@/components/policy'
import policy1 from '@/components/policy1'

import honor from '@/components/honor'
import honor1 from '@/components/honor1'

import union from '@/components/union'
import union1 from '@/components/union1'

import link from '@/components/link'
import alltext from '@/components/alltext'
import login from '@/components/login'
import login1 from '@/components/login1'
import wzd from '@/components/wzd'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/about',
      name: 'about',
      component: about,
      children:[
        {
          path:'about1',
          name:'about1',
          component:about1,
        },
        {
          path:'about2',
          name:'about2',
          component:about2,
        },
        {
          path:'about3',
          name:'about3',
          component:about3,
        }
      ]
    },
    {
      path: '/vip',
      name: 'vip',
      component: vip,
      children:[
        {
          path:'vip1',
          name:'vip1',
          component:vip1,
        },
        {
          path:'vip2',
          name:'vip2',
          component:vip2,
        },
        {
          path:'vip3',
          name:'vip3',
          component:vip3,
        }
      ]
    },
    {
      path: '/study',
      name: 'study',
      component: study,
      children:[
        {
          path: 'study1/:id',
          name: 'study1_id',
          component: study1,
        },
      ]
    },
    {
      path: '/policy',
      name: 'policy',
      component: policy,
      children:[
        {
          path: 'policy1/:a',
          name: 'policy1_a',
          component: policy1,
        }
      ]
    },
    {
      path: '/honor',
      name: 'honor',
      component: honor,
      children:[
        {
          path: 'honor1/:b',
          name: 'honor1',
          component: honor1,
        }
      ]
    },
    {
      path: '/union',
      name: 'union',
      component: union,
      children:[
        {
          path: 'union1/:c',
          name: 'union1',
          component: union1,
        }
      ]
    },
    {
      path: '/link',
      name: 'link',
      component: link
    },
    {
      path: '/alltext',
      name: 'alltext',
      component: alltext,
    },
    {
      path: '/login',
      name: 'login',
      component: login,
    },
    {
      path: '/login1',
      name: 'login1',
      component: login1,
    },
    {
      path: '/wzd',
      name: 'wzd',
      component: wzd,
    }
  ]
})



